* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  max-width: 1600px;
  width: 97%;
  min-height: 100vh;
  margin: 0 auto;
  box-shadow: 0 5px 6px #08a05ca4;
  padding: 0 1 vw;
  box-sizing: border-box;
}
/*---------------------------HEADER-----------------------------*/
header{
  width: 95%;
  min-height: 100px;
  max-width: 1530px;
  overflow: hidden;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  box-sizing: border-box;
  background-color: white;
  box-shadow: 0 5px 10px #08a05c50;
}
a{
  color: black;
  text-decoration: none;
  text-transform: uppercase;
}
a:hover{
  color: #08a05c;
}
li{
  list-style: none;
}
header ul{
  display: flex;
  padding: 0;
  margin: 0;
}
header ul li{
  padding: 0 1rem;
}
header ul li a{
  font-weight: bold;
}
header .cart{
  position: relative;
  cursor: pointer;
}
header .cart box-icon{
  width: 35px;
  height: 35px;
}
header .cart span{
  position: absolute;
  top: -1rem;
  right: -1.5rem;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  padding: 0;
  font-size: 1.5rem;
  font-weight: 700;
  background-color: crimson;
}
/*------------------INICIO----------------------*/
.inicio{
  width: 97%;
  margin: 0 auto;
  padding: .5rem ;
  color: #08a05c;
}
.inicio h1{
  padding: 9rem 0 0 0;
  font-size: 1.5rem;
  color: #08a05c;
}
.inicio img{
  width: 99%;
}
/*------------------PRODUCTOS-------------------*/
.title {
  font-size: 2.4rem;
  padding: 10rem 0 0 0;
  color: #08a05c;
}
.productos{
  padding: 5rem 1rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(22rem, 1fr));
  gap: 3rem 2rem;
  box-sizing: border-box;
}
.producto{
  display: flex;
  width: 100%;
  flex-direction: column;
  align-items: center;
  box-shadow: 1px 10px 10px 2px rgba(0, 0, 0, .2);
  border: 6px solid transparent;
  border-radius: 5px;
}
.producto:hover{
  border: 6px #08a05c50;
}
.producto__img img{
  width: 310px;
}
.producto__footer{
  padding: 1rem;
}
.producto__footer h1{
  font-weight: 600;
  font-size: 1.5rem;
  color: #1a202c;
  font-family: Helvetica, sans-serif;
}
.producto__footer p{
  font-weight: 700;
  color: rgba(0, 0, 0, .55);
  text-transform: uppercase;
  font-size: 18px;
}
.producto__footer .price{
  font-size: 2rem;
  color: #1a202c;
}
.producto .buttom{
  width: 100%;
  height: 45px;
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 1rem 3rem;
  padding: 0 1rem;
  margin: 1rem;
}
.buttom .btn{
  border: none;
  outline: 0;
  cursor: pointer;
  border-radius: 5px;
}
.buttom button{
  background-color: #4f52e6;
  max-width: 250px;
  width: 100%;
  font-size: 1.5rem;
  color: white;
  font-weight: 600;
}
.buttom div{
  background-color: #08a05c;
  display: grid;
  align-items: center;
  text-align: center;
  margin-left: -2rem;
  border-radius: 5px;
}
.buttom div a{
  color: white;
  font-weight: 700;
}
.buttom div a:hover{
  color: wheat;
}
/*-------------------CARRITO------------------*/
.carritos{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all 300ms ease-in-out;
  background-color: rgba(0, 0, 0, .45);
  z-index: 2;
  padding-bottom: 5rem;
  visibility: hidden;
}
.carrito{
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(.5);
  width: 90%;
  height: 80%;
  border-radius: 8px;
  overflow-x: scroll;
  transition: all 300ms ease-in-out;
  background-color: white;
  opacity: 0;
  visibility: hidden;
}
.carrito h2{
  text-align: center;
  font-size: 2rem;
}
.carritos.show{
  visibility: visible;
}
.carrito.show{
  transform: translate(-50%, -50%) scale(1);
  opacity: 1;
  visibility: visible;
}
.carrito__close box-icon{
  display: inline-block;
  width: 50px;
  height: 50px;
  cursor: pointer;
  transition: all 300ms ease-in-out;
  margin: 3rem 0 0 2rem;
}
.carrito__close box-icon:hover{
  fill: crimson;
}
.carrito__center{
  width: 90%;
  margin: 0 auto;

}
.carrito__item{
  display: grid;
  grid-template-columns: 1fr 2fr .4fr .4fr;
  width: 80%;
  margin: 2rem auto;
  box-shadow: 0 1px 10px #08a05c;
  border-radius: 5px;
}
.carrito__item img{
  width: 10rem;
}
.carrito__item h3{
  margin-top: 1.5rem;
  font-weight: 600;
  font-size: 1.5rem;
}
.carrito__item .price{
  color: #1a202c;
  font-size: 2rem;
  font-weight: bold;
  text-align: start;
  margin-left: -1rem;
}
.carrito__item box-icon{
  fill: #08a05c;
  width: 40px;
  height: 40px;
  cursor: pointer;
  margin-top: .3rem;
}
.carrito__item .cantidad{
  font-size: 2rem;
  font-weight: bold;
  text-align: center;
}
.carrito__item .remove__item box-icon{
  width: 50px;
  height: 50px;
  fill: crimson;
  margin-top: 3rem;
}
.carrito__footer{
  text-align: center;
  margin-bottom: 3rem;
}
.carrito__footer h3{
  font-size: 2rem;
  font-weight: 700;
  letter-spacing: 1px;
}
.carrito__footer .btn{
  display: inline-block;
  padding: .3rem 1rem;
  font-size: 1.2rem;
  background-color: #08a05c;
  color: azure;
  border: 0;
  border-radius: 3px;
  box-shadow: 5px 5px 0 rgba(0, 0, 0, .15);
  outline: 0;
  cursor: pointer;
}
.carrito__footer .btn:hover{
  color: darkturquoise;
}
/*-------------------DETALLES PRODUCTO------------------*/
.detalle{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 13rem 1rem 0 1rem;
  box-sizing: border-box;
}
.detalle h2{
  font-size: 3rem;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  font-family: Helvetica, sans-serif;
}
.detalle .price{
  font-size: 3rem;  
  width: 100%;
  color: #08a05c;
}

.detalle button{
  max-width: 300;
  margin: 1rem 0 0 0;
  padding: 1rem 1rem;
  border: none;
  outline: 0;
  background: #4f52e6;
  font-size: 1.6rem;
  color: white;
  font-weight: 600;
  border-radius: 5px;
  cursor: pointer;

}
.detalle .grid{
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem 1rem;
  max-width: 350px;
}
.detalle .grid > .nuevo, .size{
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 2rem;
  font-weight: 600;
  color: white;
}
.detalle .grid  .nuevo{
  background-color: #08a05c;
}
.detalle .grid  .size{
  background-color: crimson;
  display: flex;
}
.detalle .grid  .size select{
  border: none;
  outline: 0;
  font-size: 2rem;
  background-color: crimson;
  color: white;
  cursor: pointer;
}
.detalle .description{
  font-size: 1.1rem;
  max-width: 1300px;
  width: 100%;
  letter-spacing: 1px;
  color: #5f5d5d;
  margin: 0 auto;
  font-weight: 500;
}

.detalle .description b{
  font-size: 1.4rem;
  color: #30333a;
}

.detalle input{
  max-width: 700px;
  margin: 0 auto;
  width: 100%;
  margin-bottom: 2rem; 
}


.detalle img{
  max-width: 900px;
  min-height: 300px;
  object-fit: contain;
  margin-top: -7rem;
  z-index: -1;
}
.relacionados{
  text-align: center;
  background: #2e2e2e;
  color: aliceblue;
  max-width: 500px;
  width: 100%;
  margin: 2rem auto;
  font-size: 18px;
  letter-spacing: 3px;
  text-transform: uppercase;

}



/*-------------------MEDIA--------------------*/
@media only screen and (max-width: 1000px){
  .carrito__item{
    width: 80%;
    height: 80%;  
    padding: .5rem;
  }
 }
 @media only screen and (max-width: 450px){
  .carrito__item{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
  .carrito__item h3{
    display: none;
  }
  .carrito__item .price{
    margin-top: .9rem;
    font-weight: 700;
    font-size: 1rem;
  }
  .carrito__item img{
    transform: scale(0.5);
  }
  .carrito__item .remove__item box-icon{
    width: 30px;
    height: 30px;
  }

 }


